import * as React from 'react';
import { book } from '../../Types/common';

import { useState, useEffect } from 'react';

import { useNavigate, useParams } from 'react-router-dom';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import BookCover from '../../Components/BookCover';
import styles from './styles.module.css';
import { booksArray, yourBook } from '../../Components/BooksArray';
import { useAction } from '../../hooks/useAction';
import Counter from '../../Components/Counter';
import { stringify } from 'querystring';

export function ChoiceBook() {
  const navigate = useNavigate();
  const { setBook } = useAction();
  const choise = (book: book) => {
    setBook(book);
    navigate('/book');
  };

  return (
    <div className={styles.container}>
      <h1>Choose a book</h1>
      <ul>
        {booksArray.map((book, index) => (
          <li key={index} onClick={() => choise(book)}>
            <img className={[styles.bookCover].join(' ')} src={book.cover} />
          </li>
        ))}
      </ul>
      <button onClick={() => navigate('/addbook')}>add another book</button>
    </div>
  );
}

export function AddBook() {
  const navigate = useNavigate();
  const { setBook } = useAction();
  const [linkBook, setLinkBook] = useState('');

  return (
    <div className={styles.container}>
      <h1>Your book</h1>

      <img className={[styles.bookCover].join(' ')} src={yourBook} />

      <input
        type="text"
        placeholder="link epup"
        value={linkBook}
        onChange={(e) => setLinkBook(e.target.value)}
      />

      <div className={styles.butttonContainer}>
        <button
          onClick={() => {
            navigate('/');
          }}>
          back
        </button>
        <button
          onClick={() => {
            setBook({ cover: yourBook, header: 'your book', link: linkBook });
            navigate('/book');
          }}>
          next
        </button>
      </div>
    </div>
  );
}

