export function MoneyCount() {
  const navigate = useNavigate();

  const { pari, countDays, countHours } = useTypedSelector((state) => state.PariState);
  const { setMoney } = useAction();

  return (
    <div className={styles.container}>
      <div className="question">
        <p className={styles.questionP}>
          Are you sure, that you have enough willpower, to read so much and so quickly? How much are
          you willing to bet on this event? Hurry up to read and your money will return.
        </p>

        <Counter count={pari} setCount={setMoney} word={'near'} />

        <p className={styles.questionP}>If you bet enough, you won't need willpower.</p>
      </div>

      <div className={styles.butttonContainer}>
        <button onClick={() => navigate('/book/1')}>back</button>
        <button onClick={() => navigate('/book/3')} disabled={pari <= 0}>
          next
        </button>
      </div>
    </div>
  );
}

export function HardLevel() {
  const navigate = useNavigate();

  const { pari, countDays, countHours, test, everyday, oneBook } = useTypedSelector(
    (state) => state.PariState,
  );
  const { setTest, setEverday, setOneBook } = useAction();

  console.log(test);

  return (
    <div className={styles.containerLevel}>
      <h3>Fool-proofing</h3>

      <div className={styles.dopoptions}>
        <input type="checkbox" checked={oneBook} onChange={() => setOneBook(!oneBook)} />

        <p>Read only this book.</p>
      </div>

      <div className={styles.dopoptions}>
        <input type="checkbox" checked={everyday} onChange={() => setEverday(!everyday)} />

        <p>
          For a better study of the book,
          <br /> you need to divide reading into each day.
          <br /> Divide {countHours} hours by {countDays} days?
        </p>
      </div>

      <div className={styles.dopoptions}>
        <input type="checkbox" checked={test} onChange={() => setTest(!test)} />

        <p>
          Add nft-token test at the end of the book.
          <br />
          This will protect you from mindless page turning.
          <br />
          Choose test from trusted test authors.
        </p>
      </div>
      <div className={styles.butttonContainer}>
        <button onClick={() => navigate('/book/2')}>back</button>

        <button onClick={() => navigate('/book/4')}>next</button>
      </div>
    </div>
  );
}

export function Final() {
  const navigate = useNavigate();
  const { timer, deadline, countDays } = useTypedSelector((state) => state.PariState);
  const { setMoney, setDeadline } = useAction();

  const month = ['jan', 'feb', 'march', 'april', 'may', 'june', 'july', 'aug', 'spt', 'nvm', 'dcm'];
  // setDeadline(Date.now() + 86400000 * (countDays || localStorage.countDays));
  const date = new Date(Date.now() + 86400000 * (countDays || localStorage.countDays));
  return (
    <div className={styles.container}>
      <div className={styles.question}>
        <h3>Timer: {timer}</h3>

        <p>
          If you stop flipping pages,
          <br /> the timer will stop.
        </p>

        <h3>
          Deadline:{' '}
          {` ${date.getDate()} ${
            month[date.getMonth()]
          } ${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}  `}
        </h3>

        <p>
          If you are even a second late,
          <br /> you will lose.
        </p>
      </div>

      <div className={styles.butttonContainer}>
        <button onClick={() => navigate('/book/3')}>back</button>

        <button onClick={() => navigate('/reader')}>start reading</button>
      </div>
    </div>
  );
}
