import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { booksArray } from '../../Components/BooksArray';
import { book } from '../../Types/common';

interface PariStateType {
  countHours: number;
  countDays: number;
  pari: number;
  book: book;
  timer: number;
  deadline: number;
  oneBook: boolean;
  everyday: boolean;
  test: boolean;
}

const initialState = {
  book: { header: '', cover: '', link: '' },
  countHours: 0,
  countDays: 0,
  pari: 0,
  timer: 0,
  deadline: 0,
  oneBook: false,
  everyday: false,
  test: false,
};
const PariState = createSlice({
  name: 'PariState',
  initialState,
  reducers: {
    //first

    setOneBook(state, action: PayloadAction<boolean>) {
      state.oneBook = action.payload;
      localStorage.setItem('test', state.test ? '1' : '0');
    },
    setEverday(state, action: PayloadAction<boolean>) {
      state.everyday = action.payload;
      localStorage.setItem('everyday', state.everyday ? '1' : '0');
    },
    setTest(state, action: PayloadAction<boolean>) {
      state.test = action.payload;
      localStorage.setItem('test', state.test ? '1' : '0');
    },

    setBook(state, action: PayloadAction<book>) {
      state.book = action.payload;
      localStorage.setItem('bookHeader', state.book.header);
      localStorage.setItem('bookCover', state.book.cover);
      localStorage.setItem('bookLink', state.book.link);
    },
    setDeadline(state, action: PayloadAction<number>) {
      state.deadline = action.payload;
    },

    setTimer(state, action: PayloadAction<number>) {
      state.timer += action.payload;
      localStorage.setItem('timer', state.timer + '');
    },

    //Second

    setCountHours(state, action: PayloadAction<number>) {
      state.countHours += action.payload;
      if (state.countHours < 0) state.countHours = 0;
      localStorage.setItem('countHours', state.countHours + '');
    },

    //Third
    setCountDays(state, action: PayloadAction<number>) {
      state.countDays += action.payload;
      if (state.countDays < 0) state.countDays = 0;
      localStorage.setItem('countDays', state.countDays + '');
    },
    setMoney(state, action: PayloadAction<number>) {
      state.pari += action.payload;
      if (state.countHours < 0) state.pari = 0;

      localStorage.setItem('pari', state.pari + '');
    },
  },
});

export default PariState.reducer;
export const {
  setCountHours,
  setCountDays,
  setMoney,
  setBook,
  setTimer,
  setDeadline,
  setOneBook,
  setEverday,
  setTest,
} = PariState.actions;
