import './App.css';
import { BrowserRouter } from 'react-router-dom';
import { Routes, Route, Navigate } from 'react-router-dom';
import { Provider } from 'react-redux';
import { useTypedSelector } from './hooks/useTypedSelector';

import { store } from './Redux';

import Layout from './Components/Layout';
import {
  ChoiceBook,
  DaysCount,
  HoursCount,
  MoneyCount,
  Final,
  AddBook,
  HardLevel,
} from './Pages/Main';
import Reader from './Pages/Reader';

export function Nav() {
  const { pari } = useTypedSelector((state) => state.PariState);

  return (
    <BrowserRouter>
      <main>
        <Routes>
          <Route path="/" element={<ChoiceBook />} />

          <Route path="/book" element={<Layout />}>
            <Route index element={<HoursCount />} />
            <Route path="1" element={<DaysCount />} />
            <Route path="2" element={<MoneyCount />} />
            <Route path="3" element={<HardLevel />} />
            <Route path="4" element={<Final />} />

            <Route path="*" element={<ChoiceBook />} />
          </Route>
          <Route path="/addbook" element={<AddBook />} />
          <Route path="/reader" element={<Reader />} />
        </Routes>
      </main>
    </BrowserRouter>
  );
}

export default Nav;
