import React, { useState, useEffect } from 'react';
import { ReactReader } from 'react-reader';

import { useTypedSelector } from '../../hooks/useTypedSelector';
import { useAction } from '../../hooks/useAction';

import styles from './style.module.css';
import { useNavigate, useParams } from 'react-router-dom';

const Reader = () => {
  const navigate = useNavigate();

  // And your own state logic to persist state
  const [location, setLocation] = useState(null);
  const locationChanged = (epubcifi) => {
    // epubcifi is a internal string used by epubjs to point to a location in an epub. It looks like this: epubcfi(/6/6[titlepage]!/4/2/12[pgepubid00003]/3:0)
    setLocation(epubcifi);
  };

  // const [timer, setTimer] = useState(0);
  const [activity, setActivity] = useState(1);
  const [gas, setGas] = useState(0);
  const [runTimer, setRunTimer] = useState(true);
  const { setTimer } = useAction();
  const { timer } = useTypedSelector((state) => state.PariState);

  useEffect(() => {
    window.addEventListener('pointermove', active);
  }, []);

  const active = (e) => {
    setActivity(e.pageX);
    console.log(activity);
  };

  useEffect(() => {
    console.log('activity', activity);
    console.log('gas', gas);

    if (activity == gas) {
      setRunTimer(false);
    } else {
      setTimeout(() => {
        setGas(activity);
      }, 120000);
    }
  }, [gas]);

  useEffect(() => {
    if (runTimer) {
      setTimeout(setTimer, 1000, 1);
    }
  }, [timer, runTimer]);

  useEffect(() => {
    if (!runTimer) {
      setGas(1);
      setRunTimer(true);
    }
  }, [activity]);

  return (
    <div
      className={styles.book}
      style={{ height: '100vh' }}
      onPointerMove={(e) => active(e)}
      onPointerDown={(e) => active(e)}>
      <ReactReader
        swipeable={true}
        location={location}
        locationChanged={locationChanged}
        url="https://react-reader.metabits.no/files/alice.epub"
      />
      <div className={styles.close} onClick={() => navigate('/book/4')}>
        
      </div>
      <div className={styles.timer}>{timer}</div>
    </div>
  );
};

export default Reader;
